.slick{
    &-next,
    &-prev {
        position: absolute;
        top: 50%;
        background-color: #fff;
        opacity: .75;
        box-shadow: 2px 0 10px 2px #bbb;
        border-radius: 100%;
        z-index: 1;
        width: 60px;
        height: 60px;
        &:hover,
        &:focus {
            opacity: .75;
            background-color: white;
        }
        &:before {
            font-family: FontAwesome;
            color: #cb0024;
            padding: 24px;
            font-size: 24px;
        }
    }
    &-prev {
        &:before {
            content: "\f053";
            position: relative;
            left: -8px;
        }
    }
    &-next {
        &:before {
            content: "\f054";
        }
    }
    
}

.btn {
    &-port {
        outline: none;
        border-color: white;
        box-shadow: none;
    }
}
