.bg{
    &-sprite {
        background: url('/assets/img/css_sprites.png');
        background-size: 104px 104px;
        
    }
    &-grey{
        background-color: grey;
    }
    &-white {
        background-color: white;
    }
    &-softblue {
        background-color: aliceblue;
    }
    &-tomato {
        background-color: tomato;
    }
    &-silver{
        background: linear-gradient(to bottom, #fff, #fafafa);
    }
    &-silver1 {
        background-color: #eceff1;
    }
    &-silver2 {
        background-color: #cfd8dc;
    }
    &-silver3 {
        background-color: #b0bec5;
    }
}

.color {
    &-blue1{
        color: #e3f2fd;
    }
    &-blue2{
        color: #bbdefb;
    } 
    &-blue3 {
        color: #90caf9;
    }
    &-blue4 {
        color: #64b5f6;
    }
    &-blue5 {
        color: #42a5f5;
    }
    &-blue {
        color: #2196f3;
    }
    &-blue6 {
        color: #1e88e5;
    }
    &-blue7 {
        color: #1976d2;
    }
    &-blue8 {
        color: #1565c0;
    }
    &-blue9 {
        color: #0d47a1;
    }
}

.font {
    &-bold {
        font-weight: bold;
    }
    &-12 {
        font-size: 12px;
    }
    &-16 {
        font-size: 16px;
    }
    &-24 {
        font-size: 24px;
    }
    &-28 {
        font-size: 28px;
    }
    &-48 {
        font-size: 48px;
    }
    &-52 {
        font-size: 52px;
    }
}