.test {
    background: green;
    color: #fff;
}

.a {
    color: #000;
}

main,
html,
body {
    background-color: rgba($gold-third, .3);
    font-family: 'Lato', sans-serif;
}

main {
    padding-top: 75px;
}

h2 {
    font-family: 'Quicksand', sans-serif;
    font-size: 38px;
}

.first {
    padding: 32px 0;
    h2 {
        margin-bottom: 24px;
        color: $gold;
    }
    .top-slick {
        .col-md-3 {
            &:focus {
                outline: 0;
            }
        }
    }
}

.card {
    box-shadow: 0px 5px 10px 0px #bbb;
    // border-radius: 25px;
    margin: 0 8px;
    position: relative;
    @include breakpoint (mobile) {
        margin-bottom: 8px;
    }
    &__title {
        background: rgba($gold, .7);
        color: $gold-fourth;
        font-family: 'Quicksand', sans-serif;
        border: 4px solid white;
        text-align: right;
        padding: 16px;
        border-bottom: 0;
        font-weight: normal;
        font-size: 20px;
    }
    &__square {
        height: 32px;
        width: 32px;
        background-color: #fff;
        position: absolute;
        z-index: 9;
        top: 52px;
        left: 24px;
        transform: rotate(45deg);
    }
    &__image {
        height: 380px;
        position: relative;
        border: 4px solid white;
        .layer {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background: black;
            opacity: .4;
        }
        .border_ {
            position: absolute;
            top: 8px;
            z-index: 7;
            left: 8px;
            right: 8px;
            bottom: 8px;
            border: 2px solid white;
        }
        img {
            height: 100%;
            width: 100%;
            // border-radius: 25px 0 0 25px;
            object-fit: cover;
        }
    }
    &__button {
        color: white;
        position: absolute;
        right: 24px;
        bottom: 24px;
        border: 1px solid;
        padding: 4px 8px;
        border-radius: 16px;
        font-size: 12px;
        z-index: 8;
        &:hover {
            color: white;
            outline: 0;
            border-color: $gold;
            background-color: $gold;
            text-decoration: none;
        }
        &:focus {
            outline: 0;
        }
    }
}

.table {
    td {
        border-top: 1px solid transparent;
    }
    ul {
        margin: 0px;
        padding: 0 16px;
    }
}

.offer {
    padding: 32px 0;
    position: relative;
    background-color: $gold-third;
    h2 {
        margin-bottom: 24px;
        // color: $gold;
    }
    &__card {
        width: 45%;
        margin: 0 auto;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    &__name {
        text-align: center;
        font-size: 18px;
        margin: 16px 0;
        font-weight: bold;
        color: #212121;
    }
}

.working {
    position: relative;
    padding: 32px 0;
    &__content {
        height: 100%;
        overflow-y: hidden;
        .card {
            padding: 16px;
            .title {
                height: 65px;
                overflow: hidden;
            }
        }
    }
    &__bg {
        position: absolute;
        bottom: 0;
        right: -100px;
        overflow-y: hidden;
        width: 400px;
        opacity: .3;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            filter: grayscale(100%);
        }
    }
    h2 {
        color: $gold-secondary;
    }
}

//next page
.biaya {
    padding: 32px 0;
    label {
        margin-bottom: 0;
    }
    .border-red {
        background: $gold-secondary;
    }
}