.modal {
    &-dialog {
        max-width: 800px;
        @include breakpoint (mobile) {
            max-width: 100%;
        }
    }
    &-header {
        border-bottom: 1px solid transparent;
    }
    &-footer {
        border-top: 1px solid transparent;
    }
    &-title {
        font-family: 'Quicksand', sans-serif;
    }
    &-content {
        height: 480px;
        background: $gold-third;
        padding: 16px 36px;
        // color: white;
        // overflow-y: scroll;
    }
    &-body {
        height: 320px;
        padding: 0;
        color: white;
        overflow-y: scroll;
        &::-webkit-scrollbar-track {
            // -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            border-radius: 10px;
            background-color: #F5F5F5;
        }
        &::-webkit-scrollbar {
            width: 6px;
            background-color: #F5F5F5;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 10px;
            // -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
            background: #F5886B;
        }
        &__image {
            height: 100%;
            padding-right: 16px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__desc {
            overflow-x: hidden;
        }
    }
}