.nav {
    overflow-x: hidden;
    &-item {
        &.active {
            a {
                color: $white !important;
                background: linear-gradient(to bottom, rgba($gold, .5), $gold);
                padding: 8px;
                width: 100px;
                border-radius: 25px;
                text-align: center;
            }
        }
    }
}

.navbar {
    &-brand {
        width: 60px;
        height: 60px;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
        @include breakpoint(mobile) {
            width: 40px;
            height: 40px;
        }
    }
}