.timeline{
	width:800px;
	// background-color:#e3f2fd;
    // color:#fff;
    overflow-x: hidden;
	padding:30px 20px;
    // box-shadow:0px 0px 10px rgba(0,0,0,.5);
    @include breakpoint (mobile) {
        width:100%;
        overflow-x: hidden;
		padding:30px 5px 30px 10px;
    }
    ul{
        list-style-type:none;
        border-left:2px solid #094a68;
        padding:10px 5px;
        li {
            padding:20px 20px;
            position:relative;
            cursor:pointer;
            transition:.5s;
            &:before {
                position:absolute;
                content:'';
                width:10px;
                height:10px;
                background-color:#34ace0;
                border-radius:50%;
                left:-11px;
                top:28px;
                transition:.5s;
            }
            &:hover {
                background-color:#e8f5e9;
                &:before {
                    background-color:#2196f3;
                    box-shadow:0px 0px 10px 2px #2196f3;
                }
            }
            span {
                display:inline-block;
                background-color:#bbdefb;
                border-radius:25px;
                padding:4px 12px;
                font-size:15px;
                text-align:center;
            }
            .content {
                h3 {
                    color:#34ace0;
                    font-size:17px;
                    padding-top:5px;
                    @include breakpoint (mobile) {
                        color:#34ace0;
                        font-size:15px;
                    }
                }
                p {
                    padding:5px 0px 15px 0px;
                    font-size:15px;
                }
            }
        }
    }
}